'use strict';

import angular from 'angular';
import 'angular-material';
import mainwrap from 'common/directives/mainwrap/mainwrap';
import configService from 'config/config';
import HomeCtrl from './core.controller';
import './core.tpl';
import './coreRulesContent.tpl';
import './tableOfContents.tpl';
import './core.css!';

const coreModule = angular.module('core', [
  mainwrap.name,
  configService.name,
  'app/core/core.tpl.html',
  'app/core/coreRulesContent.tpl.html',
  'app/core/tableOfContents.tpl.html',
]);

coreModule.config( ($stateProvider) => {
  $stateProvider
    .state('core', {
      url: '/core-rules',
      templateUrl: 'app/core/core.tpl.html',
      controller: HomeCtrl,
      controllerAs: 'coreCtrl'
    });
});

export default coreModule;
